import Vue from 'vue'
import $ from 'jquery'
import mavonEditor from 'mavon-editor'
import 'mavon-editor/dist/css/index.css'
import Router from '../../router/index'
import { StorageUtil } from '../../assets/js/Auth'

Vue.config.productionTip = false

/* eslint-disable no-new */
// use
Vue.use(mavonEditor)

export default {
  name: 'write',
  beforeMount () {
    if (!StorageUtil.getObject('userInfo')) {
      Router.push('/')
    }
  },
  mounted () {
    this.getLabel()
  },
  methods: {
    saveArticle () {
      $.post('/admin/article/save', this.article, (data) => {
        console.log(data)
      })
    },
    addLabel () {
      this.send = !this.send
    },
    getLabel () {
      $.post('/admin/label/search', (data) => {
        this.commandlabel = data.labels
      })
    }
  },
  data () {
    return {
      login: StorageUtil.getObject('userInfo'),
      article: {
        value: '', // 
        title: '', // 
        abstract: '', // 
        label: '5a06abaeafcd16209447cd5a', // 
        user: StorageUtil.getObject('userInfo').id
      },
      commandlabel: [], // 
      send: false,
      toolbars: {
        bold: true, // 
        italic: true, // 
        header: true, // 
        underline: true, // 
        strikethrough: true, // 
        mark: true, // 
        superscript: true, // 
        subscript: true, // 
        quote: true, // 
        ol: true, // 
        ul: true, // 
        link: true, // 
        imagelink: true, // 
        code: true, // code
        table: true, // 
        fullscreen: false, // 
        readmodel: true, // 
        htmlcode: true, // html
        help: true, // 
        /* 1.3.5 */
        undo: true, // 
        redo: true, // 
        trash: true, // 
        save: true, // eventssave
        /* 1.4.2 */
        navigation: true, // 
        /* 2.1.8 */
        alignleft: true, // 
        aligncenter: true, // 
        alignright: true, // 
        /* 2.2.1 */
        subfield: true, // 
        preview: true // 
      }
    }
  }
}



